/*------ Tables -----*/

table {
	border-collapse: collapse;
}
.table, .text-wrap table {
	width: 100%;
	max-width: 100%;
	margin-bottom: 1rem;
}
.table th {
	padding: 0.75rem;
	vertical-align: top;
	border-top: 1px solid rgba(107, 122, 144, 0.3);
}
.text-wrap table {
	th, td {
		padding: 0.75rem;
		vertical-align: top;
		border-top: 1px solid rgba(107, 122, 144, 0.3);
	}
}
.table td {
	padding: 0.75rem;
	vertical-align: top;
	outline: 0;
}
.border {
	padding: 0.75rem;
	vertical-align: top;
	border: 1px solid rgba(107, 122, 144, 0.3);
}
.table tbody+tbody, .text-wrap table tbody+tbody {
	border-top: 2px solid #dee2e6;
}
.table .table, .text-wrap table .table, .table .text-wrap table {
	background-color: #f9f9f9;
}
.text-wrap {
	.table table, table table {
		background-color: #f9f9f9;
	}
}
.table-sm {
	th, td {
		padding: 0.3rem;
	}
}
.table-bordered, .text-wrap table {
	border: 1px solid #dee2e6;
}
.table-bordered th, .text-wrap table th, .table-bordered td, .text-wrap table td {
	border: 1px solid rgba(107, 122, 144, 0.3);
}
.table-bordered thead th, .text-wrap table thead th, .table-bordered thead td, .text-wrap table thead td {
	border-bottom-width: 1px;
}
.table-borderless {
	th, td, thead th, tbody+tbody {
		border: 0;
	}
}
.table-striped tbody tr:nth-of-type(odd) {
	background-color: rgba(0, 0, 0, 0.02);
	background: transparent;
}
.table-hover tbody tr:hover {
	background: #f9f9f9;
}
.table-primary {
	background-color: #c0bae8 !important;
	> {
		th, td {
			background-color: #c0bae8 !important;
		}
	}
}
.table-hover .table-primary:hover {
	background-color: #f9f9f9;
	> {
		td, th {
			background-color: #f9f9f9;
		}
	}
}
.table-secondary {
