.table-responsive {
	display: block;
	width: 100%;
	overflow-x: auto;
	-webkit-overflow-scrolling: touch;
	-ms-overflow-style: -ms-autohiding-scrollbar;
	>.table-bordered {
		border: 0;
	}
}
.text-wrap .table-responsive>table {
	border: 0;
}
.table-inbox {
	border: 1px solid rgba(107, 122, 144, 0.3);
	margin-bottom: 0;
	tr {
		border-bottom: 1px solid rgba(236, 234, 234, 0.9);
		&:last-child {
			border-bottom: 0;
		}
		td {
			padding: 12px !important;
			&:hover {
				cursor: pointer;
			}
			.fa-star {
				color: rgba(106, 221, 196, 0.06);
			}
		}
		&.unread td {
			background: rgba(236, 234, 234, 0.2);
			font-weight: 600;
			border-bottom: rgba(236, 234, 234, 0.9) 1px solid;
		}
	}
}
.table thead th, .text-wrap table thead th {
	border-bottom-width: 1px;
	padding-top: .5rem;
	padding-bottom: .5rem;
	vertical-align: bottom;
	border-bottom: 1px solid rgba(107, 122, 144, 0.3);
}
.table th, .text-wrap table th {
	text-transform: uppercase;
	font-size: 0.875rem;
	font-weight: 400;
}
.table-md {
	th, td {
		padding: .5rem;
	}
}
.table-vcenter {
	td, th {
		vertical-align: middle;
		border-top: 1px solid rgba(107, 122, 144, 0.3);
	}
}
.table-center {
	td, th {
		text-align: center;
	}
}
.table-striped tbody tr:nth-of-type(even) {
	background-color: #f9f9f9;
}
.table-calendar {
	margin: 0 0 .75rem;
	td, th {
		border: 0;
		text-align: center;
		padding: 0 !important;
		width: 14.28571429%;
		line-height: 2.5rem;
	}
	td {
		border-top: 0;
	}
}
.table-calendar-link {
	line-height: 2rem;
	min-width: calc(2rem + 2px);
	display: inline-block;
	border-radius: 3px;
	background: #f8f9fa;
	color: #495057;
	font-weight: 600;
	transition: .3s background, .3s color;
	position: relative;
	&:before {
		content: '';
		width: 4px;
		height: 4px;
		position: absolute;
		right: .25rem;
		top: .25rem;
		border-radius: 50px;
	}
	&:hover {
		color: $white;
		text-decoration: none;
		transition: .3s background;
		&:before {
			background: $white;
		}
	}
}
.table-header {
	cursor: pointer;
	transition: .3s color;
	&:hover {
		color: #495057 !important;
	}
	&:after {
		content: '\f0dc';
		font-family: FontAwesome;
		display: inline-block;
		margin-right: .5rem;
		font-size: .75rem;
	}
}
.table-header-asc {
	color: #495057 !important;
	&:after {
		content: '\f0de';
	}
}
.table-header-desc {
	color: #495057 !important;
	&:after {
		content: '\f0dd';
	}
}
.table.card-table.border td {
	vertical-align: inherit !important;
}
div.dataTables_wrapper div.dataTables_filter input {
	margin-right: 0.5em !important;
	margin-left: auto !important;
	display: inline-block;
	width: auto;
	height: calc(1.8125rem + 2px);
}
.text-wrap table {
	th, td {
		border-top: 0 !important;
	}
}
.card-table.table th {
    border-top: 0px ;
}
div {
	&.dataTables_wrapper div.dataTables_filter {
	  text-align: left !important;
	}
  
	&.table-responsive > div.dataTables_wrapper > div.row > div[class^="col-"]:last-child {
	  padding-left: 0 !important;
	  padding-right: auto !important;
	}
  }
  
  table {
	&.dataTable thead {
	  .sorting:before, .sorting_asc:before, .sorting_desc:before, .sorting_asc_disabled:before, .sorting_desc_disabled:before {
		left: 1em !important;
		right: auto !important;
		content: "\2191";
	  }
  
	  .sorting:after, .sorting_asc:after, .sorting_desc:after, .sorting_asc_disabled:after, .sorting_desc_disabled:after {
		left: 0.5em !important;
		right: auto !important;
		content: "\2193";
	  }
  
	  > tr > {
		th {
		  &.sorting_asc, &.sorting_desc, &.sorting {
			padding-left: 30px;
			padding-right: auto;
		  }
		}
  
		td {
		  &.sorting_asc, &.sorting_desc, &.sorting {
			padding-left: 30px;
			padding-right: 10 !important;
		  }
		}
	  }
	}
  
	&.table-bordered.dataTable {
	  th, td {
		border-right-width: 0;
		border-left-width: thin !important;
	  }
	}
  }
  
div.table-responsive>div.dataTables_wrapper>div.row>div[class^="col-"]:first-child {
    padding-right: 0 !important;
}
