/*-----Jumbotron-----*/

.jumbotron {
	padding: 2rem 1rem;
	margin-bottom: 2rem;
	background-color: rgba(241, 240, 245, 0.5);
	border-radius: 3px;
}
@media (min-width: 576px) {
	.jumbotron {
		padding: 2rem;
	}
}
.jumbotron-fluid {
	padding-right: 0;
	padding-left: 0;
	border-radius: 0;
}
/*-----Jumbotron-----*/